import "./StepByStepBlock.style.scss";

export function StepByStepBlock({
  svgComp,
  titleText,
  titleSpan,
  description,
}) {
  return (
    <>
      <div className="stepByStepContainer">
        {svgComp}
        <div className="webdev-section-one-desc-container">
          <h3>
            {titleText} <span className="text-span-brown">{titleSpan}</span>
          </h3>
          <p>{description}</p>
        </div>
      </div>
    </>
  );
}
