import "./h1.style.scss";

export function H1({ spantext, text, classes, spanClass }) {
  return (
    <>
      <div className="glitch-wrapper">
        <h1 className={`glitch ${classes}`} data-glitch={spantext + text}>
          <span className={spanClass}>{spantext}</span>
          {text}
        </h1>
      </div>
    </>
  );
}
