import "./SectionTwo.style.scss";
import { CategoryBox } from "./../../CategoryBox.component";
import {
  SVGDisplayCode,
  SVGPencilRuler,
  SVGPuzzleAlt,
} from "../../../containers/SVGContainer.container";
import { Link } from "react-router-dom";

export function SectionTwo() {
  return (
    <>
      <div id="ancor1" className=" sectionTwoContainer CategoryBoxContainer">
        <Link to={"/webdevelopment"}>
          <CategoryBox
            id={"cb1"}
            svgComponent={<SVGDisplayCode classNames={"svg80"} glitch={true} />}
            spantext={"Web"}
            text={"development"}
          />
        </Link>

        <Link to={"/teaching"}>
          <CategoryBox
            id={"cb2"}
            svgComponent={<SVGPuzzleAlt classNames={"svg80"} glitch={true} />}
            spantext={"Tea"}
            text={"ching"}
          />
        </Link>
        <Link to={"/skills"}>
          <CategoryBox
            id={"cb3"}
            svgComponent={<SVGPencilRuler classNames={"svg80"} glitch={true} />}
            spantext={"Too"}
            text={"ls"}
          />
        </Link>
      </div>
    </>
  );
}
