import { ScrollRestoration } from "react-router-dom";
import { Footer } from "../components/Footer/Footer.component";
import { ContactForm } from "../components/Forms/ContactForm.component";
import { HeroSectionSubPage } from "../components/HeroSection/HeroSectionSubPage.component";

import "./Contact.style.scss";

export function Contact() {
  return (
    <>
      <HeroSectionSubPage text={"CONTACT"} span={"ME"} />
      <div className="contact-section-two">
        <div className="contact-section-two-background"></div>
        <div className="contact-section-two-background-layer">
          <div className="container form-container">
            <ContactForm
              mtSpan={"ME"}
              mtText={"SSAGE"}
              stText={"WRITE ME A"}
              stSpan={"MESSAGE"}
              placeholderSubject={"I want to talk about..."}
              placeholderEmail={"my.nice@email.com"}
              placeholderTextarea={"This is my beautiful story..."}
              formspreeFormid={"mpzgavgg"}
              spanColorClass={"text-span"}
            />
          </div>
        </div>
      </div>
      <Footer />
      <ScrollRestoration />
    </>
  );
}
