import {
  SVGGithub,
  SVGHearth,
  SVGUdemy,
} from "../../containers/SVGContainer.container";
import "./Footer.style.scss";

const date = new Date().getFullYear();

export function Footer() {
  return (
    <>
      <div className="footer-sides-container">
        <div className="footer-left">
          <h5 className="footer-main-title">
            <span className="text-span">DEV</span>CODINO
          </h5>
          <small className="copyright-container">
            ©{date} - With
            <span>
              <SVGHearth classNames={"svg16 footer-hearth"} glitch={true} />
            </span>
            by Devcodino
          </small>
        </div>
        <div className="footer-right">
          <a
            href="https://www.udemy.com/user/markus-lech-22/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <SVGUdemy classNames={"svg24 footer-social-icon"} glitch={true} />
          </a>

          <a
            href="https://github.com/Devcodino"
            target="_blank"
            rel="noopener noreferrer"
          >
            <SVGGithub classNames={"svg24 footer-social-icon"} glitch={true} />
          </a>
        </div>
      </div>
    </>
  );
}
