import { SVGArrowDown } from "../../containers/SVGContainer.container";
import "./HeroSection.style.scss";

export function HeroSection({
  backgroundClass,
  titleComponents,
  ctaTextLeft,
  ctaTextRight,
  svgFillClass,
}) {
  return (
    <>
      <div className="herosection">
        <div className={`${backgroundClass} background`}></div>
        <div className="herosection-background-layer">
          <div className="container herosection-content">
            <div className="herosection-title-container">
              {titleComponents.map((component, index) => {
                return <div key={index}>{component}</div>;
              })}
            </div>
            <div className="herosection-cta-container">
              <a href={"#ancor1"} className="devc-icon-btn-blue">
                <h5>{ctaTextLeft}</h5>
                <SVGArrowDown
                  classNames={`svg80 herosection-cta-svg ${svgFillClass}`}
                  glitch={true}
                />
                <h5>{ctaTextRight}</h5>
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
