import "./TextArea.style.scss";

export function TextArea({
  rows,
  classNames,
  placeholder,
  value,
  onFocusHandler,
  onBlurHandler,
}) {
  return (
    <>
      <textarea
        onFocus={onFocusHandler}
        onBlur={onBlurHandler}
        rows={rows}
        className={`input-textarea ${classNames}`}
        placeholder={placeholder}
        value={value}
        name="message"
        required
      ></textarea>
    </>
  );
}
