import { HeroSection } from "../components/HeroSection/HeroSection.component";
import "./Webdevelopment.style.scss";
import { H1 } from "../components/Titles/h1.component";
import {
  SVGPuzzleAlt,
  SVGMessages,
} from "./../containers/SVGContainer.container";
import { StepByStepBlock } from "../components/StepByStepBlock.component";
import { ContactForm } from "../components/Forms/ContactForm.component";
import { ScrollRestoration } from "react-router-dom";
import { useState } from "react";

import { EmailForm } from "../components/Forms/EmailForm.component";
import { Footer } from "../components/Footer/Footer.component";
import { BlackBoxCTA } from "../components/BlackBoxCTA.component";

export function Webdevelopment() {
  const [formFocus, setFormFocus] = useState(false);

  const onFocusHandler = () => {
    setFormFocus(true);
  };

  const onBlurHandler = () => {
    setFormFocus(false);
  };

  return (
    <>
      <HeroSection
        backgroundClass={"background2"}
        titleComponents={[
          <p className="ml mb herosection-title pH4">
            HOW <span className="text-span-brown">TO</span> GET{" "}
            <span className="text-span-brown">YOUR</span>
          </p>,
          <H1
            spantext={""}
            text={"WEBSITE"}
            classes={"herosection-big-title"}
          />,
        ]}
        ctaTextLeft={"MORE"}
        ctaTextRight={"INFO"}
        svgFillClass={"fill-brown"}
      />
      <div id="ancor1" className="webdev-section-one">
        <StepByStepBlock
          svgComp={<SVGPuzzleAlt classNames={"svg150"} glitch={true} />}
          titleText={"COLLECT"}
          titleSpan={"IDEAS"}
          description={
            "Tell me what ideas you already have. What do you want your website to do? What goal do you want to achieve with your website."
          }
        />
        <StepByStepBlock
          svgComp={<SVGMessages classNames={"svg150"} glitch={true} />}
          titleText={"SEND A"}
          titleSpan={"MESSAGE"}
          description={
            "Tell me what ideas you already have. What do you want your website to do? What goal do you want to achieve with your website. I will get back to you as soon as possible. We'll talk about your project, the programming and implementation process and the price."
          }
        />
      </div>
      <div className="webdev-section-two">
        <div className="container webdev-section-two-content">
          <img
            style={{ marginRight: formFocus ? "-6.875em" : "-25.250em" }}
            className="webdev-section-two-monster"
            src="./Twurks-Monster.webp"
            alt="A cute, fluffy purple Devcodino Monster"
          ></img>
          <ContactForm
            mtSpan={"ME"}
            mtText={"SSAGE"}
            stText={"WRITE ME A"}
            stSpan={"MESSAGE"}
            placeholderSubject={"I want to talk about..."}
            placeholderEmail={"your@mail.com"}
            placeholderTextarea={"This is your idea..."}
            formspreeFormid={"mpzgavgg"}
            classNames={"webdev-contact-form"}
            spanColorClass={"text-span-brown"}
            onFocusHandlerInput={onFocusHandler}
            onFocusHandlerInputTextArea={onFocusHandler}
            onBlurHandlerInput={onBlurHandler}
            onBlurHandlerTextarea={onBlurHandler}
          />
        </div>
      </div>
      <BlackBoxCTA
        backgroundClass={"background1"}
        components={[
          <h3>
            <span className="text-span-brown">NEWS</span>LETTER
          </h3>,
          <div className="container email-form-container">
            <EmailForm formspreeFormid={"mknlwloq"} />
          </div>,
        ]}
      />
      <Footer />
      <ScrollRestoration />
    </>
  );
}
