import { useForm } from "@formspree/react";
import { ButtonSend } from "../ButtonSend.component";
import { Input } from "../Input.component";
import { TextArea } from "../TextArea.component";
import "./ContactForm.style.scss";
import { Form } from "react-router-dom";

export function ContactForm({
  mtSpan,
  mtText,
  stSpan,
  stText,
  placeholderSubject,
  placeholderEmail,
  placeholderTextarea,
  formspreeFormid,
  classNames,
  spanColorClass,
  onFocusHandlerInput,
  onFocusHandlerInputTextArea,
  onBlurHandlerInput,
  onBlurHandlerTextarea,
}) {
  const [state, handleSubmit] = useForm(formspreeFormid);
  if (state.succeeded) {
    return (
      <div className={`form-content-container ${classNames}`}>
        <p>Thanks for get in touch!</p>
      </div>
    );
  }

  return (
    <>
      <div className={`form-content-container ${classNames}`}>
        <div className="form-titles">
          <h4>
            <span className={spanColorClass}>{mtSpan}</span>
            {mtText}
          </h4>
          <p>
            {stText} <span className={spanColorClass}>{stSpan}</span>
          </p>
        </div>
        <Form onSubmit={handleSubmit}>
          <div className="inputs-container">
            <Input
              onFocusHandler={onFocusHandlerInput}
              onBlurHandler={onBlurHandlerInput}
              type={"text"}
              name={"subject"}
              placeholder={placeholderSubject}
              arialabelText={"This is the subject input"}
              formspreeErrors={state.errors}
            />
            <Input
              onFocusHandler={onFocusHandlerInput}
              onBlurHandler={onBlurHandlerInput}
              type={"email"}
              name={"email"}
              placeholder={placeholderEmail}
              arialabelText={"This is the email input"}
              formspreeErrors={state.errors}
            />
          </div>
          <TextArea
            onFocusHandler={onFocusHandlerInputTextArea}
            onBlurHandler={onBlurHandlerTextarea}
            rows={"10"}
            placeholder={placeholderTextarea}
          />
          <div className="sendbutton-container">
            <ButtonSend formspreeSubmitting={state.submitting} />
          </div>
        </Form>
      </div>
    </>
  );
}
