import { SVGVSCode } from "../../../containers/SVGContainer.container";
import { PluginShowCase } from "../../PluginShowCase.component";
import "./SectionSix.style.scss";

export function SectionSix() {
  return (
    <>
      <div className="sectionSix">
        <div className="container sectionSixContent">
          <PluginShowCase
            to={
              "https://marketplace.visualstudio.com/items?itemName=tonybaloney.vscode-pets"
            }
            img={"icon.webp"}
            alt={"VSCode Pets Icon"}
            span={"VS CODE"}
            text={"PETS"}
          />
          <div className="sectionSixTitleContainer">
            <SVGVSCode classNames={"svg64"} glitch={true} />
            <h3>
              <span className="text-span">NICE</span> PLUGINS
            </h3>
          </div>

          <PluginShowCase
            to={
              "https://marketplace.visualstudio.com/items?itemName=BeardedBear.beardedtheme"
            }
            img={"icon2.webp"}
            alt={"Bearded Theme Icon"}
            span={"BEARDED"}
            text={"THEME"}
          />
        </div>
      </div>
    </>
  );
}
