import "./CategoryBox.style.scss";

export function CategoryBox({ id, svgComponent, spantext, text, glitch }) {
  return (
    <>
      <div id={id} className="CategoryBox">
        <div className="background"></div>
        <div className="backgroundLayer">
          <div className="CategoryBoxContent">
            {svgComponent}
            <h5>
              <span className="text-span">{spantext}</span>
              {text}
            </h5>
          </div>
        </div>
      </div>
    </>
  );
}
