import { ReactComponent as DisplayCodeSVG } from "../fi-sr-display-code.svg";
import { ReactComponent as PencilRulerSVG } from "../fi-sr-pencil-ruler.svg";
import { ReactComponent as PuzzleAltSVG } from "../fi-sr-puzzle-alt.svg";
import { ReactComponent as ArrowDownSVG } from "../fi-sr-arrow-square-down.svg";
import { ReactComponent as MessagesSVG } from "../fi-sr-messages.svg";
import { ReactComponent as CSSSVG } from "../fi-brands-css3-alt.svg";
import { ReactComponent as FigmaSVG } from "../fi-brands-figma.svg";
import { ReactComponent as HTMLSVG } from "../fi-brands-html5.svg";
