import { GlitchHover } from "../powerglitch/GlitchHover";

export function SVGDisplayCode({ classNames, glitch }) {
  return (
    <>
      {glitch ? (
        <div ref={GlitchHover().ref}>
          <DisplayCodeSVG className={classNames} />
        </div>
      ) : (
        <DisplayCodeSVG className={classNames} />
      )}
    </>
  );
}

