import {
  SVGVSCode,
  SVGNode,
  SVGHTML,
  SVGCSS,
  SVGJS,
  SVGReact,
  SVGPhp,
  SVGFigma,
} from "../../../containers/SVGContainer.container";
import "./SectionFour.style.scss";

export function SectionFour() {
  return (
    <>
      <div className="sectionFourContainer">
        <SVGVSCode classNames={"svg64"} glitch={true} />
        <SVGNode classNames={"svg64"} glitch={true} />
        <SVGHTML classNames={"svg64"} glitch={true} />
        <SVGCSS classNames={"svg64"} glitch={true} />
        <SVGJS classNames={"svg64"} glitch={true} />
        <SVGReact classNames={"svg64"} glitch={true} />
        <SVGPhp classNames={"svg64"} glitch={true} />
        <SVGFigma classNames={"svg64"} glitch={true} />
      </div>
    </>
  );
}
