import { Typewriter } from "react-simple-typewriter";
import "./WorkInProgress.style.scss";

export function WorkInProgress() {
  return (
    <>
      <div className="container wip-container">
        <h3>
          <span className="text-span">WORK</span>
          <Typewriter
            words={[" IN PROGRESS", " LOADING", " BUILDING"]}
            loop={0}
            typeSpeed={150}
            deleteSpeed={120}
            delaySpeed={5000}
            cursor={true}
            cursorStyle="|"
            cursorBlinking={true}
          />
        </h3>
      </div>
    </>
  );
}
