import {
  createBrowserRouter,
  createRoutesFromElements,
  Route,
  RouterProvider,
} from "react-router-dom";
import { Home } from "./views/home.view";
import { WorkInProgress } from "./views/WorkInProgress.view";
import { Contact } from "./views/Contact.view";
import { Webdevelopment } from "./views/Webdevelopment.view";
import { Teaching } from "./views/Teaching.view";

const router = createBrowserRouter(
  createRoutesFromElements(
    <>
      <Route path="/">
        <Route index element={<Home />} />
      </Route>
      <Route path="/webdevelopment" element={<Webdevelopment />} />
      <Route path="/teaching" element={<Teaching />} />
      <Route path="/skills" element={<WorkInProgress />} />
      <Route path="/contact" element={<Contact />} />
      <Route path="*" element={<p>Seite nicht vorhanden</p>} />
    </>
  )
);

function App() {
  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
