import "./BlackBoxCTA.style.scss";

export function BlackBoxCTA({ components, backgroundClass }) {
  return (
    <>
      <div className={`blackBoxCTAContainer ${backgroundClass}`}>
        <div className="blackBoxCTAContentContainer">
          {components.map((component, index) => {
            return <div key={index}>{component}</div>;
          })}
        </div>
      </div>
    </>
  );
}
