import { SVGSend } from "../containers/SVGContainer.container.jsx";
import "./ButtonSend.style.scss";

export function ButtonSend({ formspreeSubmitting }) {
  return (
    <>
      <button
        disabled={formspreeSubmitting}
        className="submit-btn-inline"
        type="submit"
      >
        <SVGSend classNames={"svg46 send-icon"} glitch={false} />
      </button>
    </>
  );
}
