import "./PluginShowCase.style.scss";

export function PluginShowCase({ to, img, alt, span, text }) {
  return (
    <>
      <div className="pluginItem">
        <a href={to} target="_blank" rel="noopener noreferrer">
          <div className="img-container">
            <img src={img} alt={alt} />
          </div>
        </a>
        <p>
          <span className="text-span">{span}</span> {text}
        </p>
      </div>
    </>
  );
}
