export function Teaching() {
  return (
    <>
      <HeroSection
        backgroundClass={"background4"}
        titleComponents={[
          <p className="pH4">
            TIME <span className="text-span-yellow">TO</span> LEARN
          </p>,
          <H1
            spantext={"COD"}
            text={"ING"}
            classes={"herosection-big-title"}
            spanClass={"text-span-yellow"}
          />,
        ]}
        svgFillClass={"fill-yellow"}
      />
      <div id="ancor1" className="container teaching-section-two">
        <h4 className="teaching-section-two-title">
          CHECK MY <span className="text-span-yellow">UDEMY</span> COURSES
        </h4>
        <AliceCarousel
          keyboardNavigation
          animationDuration={2000}
          responsive={{
            0: {
              items: 1,
            },
            1024: {
              items: 3,
              itemsFit: "contain",
            },
          }}
          mouseTracking
          touchTracking
          items={items}
        />
      </div>
      <BlackBoxCTA
        backgroundClass={"background2"}
        components={[
          <h4>
            IMPROVE <span className="text-span-yellow">YOUR</span>
          </h4>,
          <H1
            spantext={""}
            text={"SKILLS"}
            classes={"teaching-section-three-h1"}
          />,
        ]}
      />
      <div className="teaching-section-four">
        <h4>
          I AM <span className="text-span-yellow">TEACHER</span> AT
        </h4>
        <SVGDCI classNames={"svgDCI fill-white"} glitch={true} />
        <CTAButton
          to={
            "https://share.hsforms.com/19sY1ieyWRqezdj92Oo4FFA457wo?referred_by=ba843644ce2c4e6d901f860334736f89&utm_campaign=Markus-Lech&referred_by_which_dci_friend_category=Employee&utm_medium=referral-Employee"
          }
          text={"Check it out"}
        />
      </div>
      <BlackBoxCTA
        backgroundClass={"background1"}
        components={[
          <h3>
            <span className="text-span-yellow">GET </span>UPDATES
          </h3>,
          <div className="container email-form-container">
            <EmailForm formspreeFormid={"mknlwloq"} />
          </div>,
        ]}
      />
      <Footer />
      <ScrollRestoration />
    </>
  );
}
