import { useForm } from "@formspree/react";
import { ButtonSend } from "../ButtonSend.component";
import { Input } from "../Input.component";
import "./EmailForm.style.scss";

export function EmailForm({ formspreeFormid }) {
  const [state, handleSubmit] = useForm(formspreeFormid);
  if (state.succeeded) {
    return <p>Thanks for joining!</p>;
  }

  return (
    <>
      <form onSubmit={handleSubmit} key={"get-contact"} className="email-form">
        <Input
          type={"email"}
          name={"email"}
          formspreeErrors={state.errors}
          placeholder={"your@email.com"}
          arialabelText={
            "Write your email here, to get updates about my projects"
          }
        />
        <ButtonSend formspreeSubmitting={state.submitting} />
      </form>
    </>
  );
}
