import "./Input.style.scss";

export function Input({
  type,
  name,
  placeholder,
  arialabelText,
  formspreeErrors,
  onFocusHandler,
  onBlurHandler,
}) {
  return (
    <>
      <div className="devc-input-container">
        <input
          onFocus={onFocusHandler}
          onBlur={onBlurHandler}
          className="devc-text-input"
          type={type}
          name={name}
          errors={formspreeErrors}
          placeholder={placeholder}
          aria-label={arialabelText}
          required
        ></input>
      </div>
    </>
  );
}
